@import "typography";
@import "variables";
@import "mixin";

// Required
@import "./node_modules/bootstrap/scss/functions";
@import "./node_modules/bootstrap/scss/variables";
@import "./node_modules/bootstrap/scss/mixins";

// Optional

@import "./node_modules/bootstrap/scss/root";
@import "./node_modules/bootstrap/scss/reboot";
@import "./node_modules/bootstrap/scss/bootstrap-utilities";
@import "./node_modules/bootstrap/scss/bootstrap-grid";
@import "./node_modules/bootstrap/scss/card";
@import "./node_modules/bootstrap/scss/list-group";
@import "./node_modules/bootstrap/scss/tables";
@import "./node_modules/bootstrap/scss/alert";
@import "./node_modules/bootstrap/scss/nav";
@import "./node_modules/bootstrap/scss/navbar";
@import "./node_modules/bootstrap/scss/pagination";
@import "./node_modules/bootstrap/scss/transitions";
@import "./node_modules/bootstrap/scss/images";
@import "./node_modules/bootstrap/scss/type";
@import "./node_modules/bootstrap/scss/buttons";
@import "./node_modules/bootstrap/scss/forms";

@import "./public/assets/icons/flaticons/font/_flaticon";

html {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.transition{
    @include transition();
}
a,
button,
*:before,
*:after,
label,
.transition {
    @include transition;
}
.valign {
    @include valign();
}
.container-fluid {
    max-width: 1200px;
}
.container-fluid.wide {
    max-width: 1400px !important;
}
.img-full {
    width: 100%;
    height: auto;
    display: block;
}


/**
*   BEGIN: Fonts
*/
.font-family-base {
    font-family: $font-family-base;
}
.font-family-primary {
    font-family: $font-family-primary;
}

.light {
    font-weight: $font-weight-light;
}
.regular {
    font-weight: $font-weight-regular;
}
.medium {
    font-weight: $font-weight-medium;
}
.bold {
    font-weight: $font-weight-bold;
}
/*
*   END: Fonts
*/



/**
*   BEGIN: Fonts Size
*/
.xx-small {
    font-size: $font-size-xx-small;
}
.x-small {
    font-size: $font-size-x-small;
}
.small {
    font-size: $font-size-small;
}
.small-n {
    font-size: $font-size-small-n;
}
.normalsize {
    font-size: $font-size-normalsize;
}
.large {
    font-size: $font-size-large;
}
.x-large {
    font-size: $font-size-x-large;
}
.xx-large {
    font-size: $font-size-xx-large;
}
.xxx-large {
    font-size: $font-size-xxx-large;
}
.big {
    font-size: $font-size-big;
}
.x-big {
    font-size: $font-size-x-big;
}
.xx-big {
    font-size: $font-size-xx-big;
}
.xxx-big {
    font-size: $font-size-xxx-big;
}
/**
*   END: Fonts Size
*/
