@keyframes pulse {
    0% {
        opacity: 100%;
        width: 4.5rem;
        height: 4.5rem;
    }
    5% {
        opacity: 90%;
        width: 4.5rem;
        height: 4.5rem;
    }
    100% {
        opacity: 0;
        width: 8rem;
        height: 8rem;
    }
}
.loading-display{
    display: block;
}
.loading-content{
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;

    &::before{
        content: '';
        position: absolute;
        background-color: $dark;
        opacity: .75;
        width: 100%;
        height: 100%;
    }

    .loading-wrapper {
        width: 8rem;
        height: 8rem;
        margin: 0 auto;
        position: relative;
        @include valign;
    
        .loading-pulse {
            background-color: $primary;
            padding: .8rem;
            border-radius: 50%;
            width: 4rem;
            height: 4rem;
            position: relative;
        
            &::after{
                content: '';
                position: absolute;
                background-color: $primary;
                opacity: 70%;
                width: 5rem;
                height: 5rem;
                z-index: -1;
                left: 50%;
                top: 50%;
                border-radius: 50%;
                transform: translate(-50%,-50%);
                animation: pulse 1s ease-out infinite;
            }
        
            img{
                max-width: 100%;
                display: block;
                margin: 0 auto;
                @include valign;
            }
        }

        .loading-label{
            position: absolute;
            bottom: -30px;
            color: #fff;
            font-weight: 700;
            text-align: center;
            width: 100%;
            line-height: 17px;
        }
    }
}




